import { DOCUMENT } from '@angular/common';
import { Component, Inject, Renderer2, RendererFactory2} from '@angular/core';
import { Meta } from '@angular/platform-browser';
import { SeoService } from 'src/app/services/seo/seo.service';


@Component({
  selector: 'app-home',
  templateUrl: './landing.component.html',
  styleUrls: ['./landing.component.css'],
})
export class LandingComponent {
  private renderer: Renderer2;
  constructor(
    private seoSvc: SeoService,
    @Inject(DOCUMENT) private dom: any,
    private meta: Meta,
    private rf2: RendererFactory2
  ) {
    this.renderer = rf2.createRenderer(null, null);
    this.seoSvc.setMetaTitle(
      `Leadgen Tools, Free Tools, Sales Tools, Lead Tools | Clodura.AI`
    );
    this.seoSvc.setMetaOrgTitle(
      `Leadgen Tools, Free Tools, Sales Tools, Lead Tools | Clodura.AI`
    );
    this.seoSvc.setMetaDescription(
      `Free and AI-powered lead generation tools, such as Clodura.AI, can be used to identify and qualify potential customers, generate personalized content, and verify the accuracy of contact information. These tools can be a valuable resource for businesses of all sizes, regardless of their budget or experience.`
    );
    this.seoSvc.setMetaOrgDesc(
      `Free and AI-powered lead generation tools, such as Clodura.AI, can be used to identify and qualify potential customers, generate personalized content, and verify the accuracy of contact information. These tools can be a valuable resource for businesses of all sizes, regardless of their budget or experience.`
    );
    this.seoSvc.setMetaKey(`
          Sales Tools, Lead Generation Tools, LeadGen Tools, Free Tools, Sales Tools, Free Sales Tools, AI Powered, AI Powered LeadGen Tools, Verification Tools, Content Generation, Clodura.AI, Email Validation, Built With, email verifier, free email verifier, verify email addresses, email validation, clean email list, bulk email verification, improve deliverability, email marketing, validate emails, email list cleaning, email verification tool, check email validity.
          `);
    this.createCanonicalURL();
    this.updateHrefLangs(`https://www.clodura.ai/tools`);
  }

  public createCanonicalURL() {
    const head = this.dom.getElementsByTagName('head')[0];
    let element: HTMLLinkElement =
      this.dom.querySelector(`link[rel='canonical']`) || null;
    if (element == null) {
      element = this.dom.createElement('link') as HTMLLinkElement;
      head.appendChild(element);
    }
    element.setAttribute('rel', 'canonical');
    element.setAttribute('href', 'https://www.clodura.ai/tools');
  }

  public updateHrefLangs(url: string) {
    // let link: HTMLLinkElement = this.dom.createElement("link");
    // link.setAttribute("rel", "canonical");
    // this.dom.head.appendChild(link);
    // link.setAttribute("href", this.dom.URL.replace("http://", "https://"));
    // console.log(link, this.dom.URL);
    const arr = Array.from(this.dom.head.children);
    const linkElt = arr.find(
      (e: any) => e['rel'] === 'alternate' && e['hreflang'] === 'x-default'
    );
    const linkElten = arr.find(
      (e: any) => e['rel'] === 'alternate' && e['hreflang'] === 'en'
    );
    this.renderer.setAttribute(linkElten, 'href', url);
    this.renderer.setAttribute(linkElt, 'href', url);
  }
}
