import { ChangeDetectorRef, Component, Inject, OnInit, Renderer2, RendererFactory2, ViewChild } from '@angular/core';
import { environment } from '../../../../environments/environment';
import { SeoService } from '../../../services/seo/seo.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { PLATFORM_ID } from '@angular/core';
import { DOCUMENT, isPlatformBrowser } from '@angular/common';
import {ReCaptcha2Component} from 'ngx-captcha';
import { Meta } from '@angular/platform-browser';


export interface FormModel {
  captcha?: string;
}
@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css'],
})
export class HomeComponent implements OnInit {
  @ViewChild('captchaElem') captchaElem: ReCaptcha2Component | any;
  public siteKey?: string = environment.application.gtwoSiteKey;
  public captchaIsLoaded = false;
  public captchaSuccess = false;
  public captchaIsExpired = false;
  public captchaResponse?: string;
  isBrowser: boolean;

  public theme: 'light' | 'dark' = 'light';
  public size: 'compact' | 'normal' = 'normal';
  public lang = 'en';
  public type: 'image' | 'audio' = 'image';
  public useGlobalDomain: boolean = false;
  name = environment.application.name;
  // sitekey = environment.application.gsiteKey;
  // secretKey = environment.application.secretKey;
  email = '';
  token = '';
  errMsg = {
    msg: '',
    type: '',
  };
  loadnsub = {
    loading: false,
    submit: false,
    reset: false,
    isError: false,
    btnLabel: 'Verify Email',
  };
  public formModel: FormModel = {};
  public aFormGroup: FormGroup;
  public valresp: any;
  private renderer: Renderer2;
  constructor(
    @Inject(PLATFORM_ID) platformId: any,
    private seoService: SeoService,
    private formBuilder: FormBuilder,
    private cdr: ChangeDetectorRef,
    @Inject(DOCUMENT) private dom: any,
    private meta: Meta,
    private rf2: RendererFactory2,
    private seoSvc: SeoService
  ) {
    this.renderer = rf2.createRenderer(null, null);
    this.seoSvc.setMetaTitle(
      `Free Email Verifier Tool | Instantly Verify Email Addresses | Clodura.AI`
    );
    this.seoSvc.setMetaOrgTitle(
      `Free Email Verifier Tool | Instantly Verify Email Addresses | Clodura.AI`
    );
    this.seoSvc.setMetaDescription(
      `Clean and validate your email list with Clodura.AIâ€™s free email verifier tool. Instantly verify email addresses for better deliverability, engagement, and cost savings. No sign-up required.`
    );
    this.seoSvc.setMetaOrgDesc(
      `Clean and validate your email list with Clodura.AIâ€™s free email verifier tool. Instantly verify email addresses for better deliverability, engagement, and cost savings. No sign-up required.`
    );
    this.seoSvc.setMetaKey(`
          email verifier, free email verifier, verify email addresses, email validation, clean email list, bulk email verification, improve deliverability, email marketing, validate emails, email list cleaning, email verification tool, check email validity.
          `);

    this.createCanonicalURL();
    this.updateHrefLangs(`https://www.clodura.ai/tools/email-verifier`);
    this.isBrowser = isPlatformBrowser(platformId);
    this.aFormGroup = this.formBuilder.group({
      recaptcha: ['', Validators.required],
      email: [
        '',
        [
          Validators.required,
          Validators.pattern(
            /^[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?$/
          ),
        ],
      ],
    });
  }
  ngOnInit() {
    console.log('');
  }

  verify(form: FormGroup) {
    this.loadnsub.submit = true;
    this.loadnsub.reset = true;
    if (form.valid) {
      this.loadnsub.loading = true;
      this.loadnsub.btnLabel = 'Verifying';
      this.seoService.validateEmail(form.value).subscribe((res) => {
        this.loadnsub.loading = false;
        this.loadnsub.submit = false;
        if (!res.success) {
          this.loadnsub.isError = true;
          this.errMsg.msg = res.err;
          this.errMsg.type = res.type;
          this.loadnsub.btnLabel = 'Error';
        } else {
          this.valresp = res;
          if (this.valresp.valid) {
            this.loadnsub.btnLabel = 'Valid';
          } else {
            this.loadnsub.btnLabel = 'Invalid';
          }
        }
        setTimeout(() => {
          this.loadnsub.reset = false;
          this.loadnsub.btnLabel = 'Verify Email';
          this.aFormGroup.reset();
          // this.handleReset();
          this.captchaElem.reloadCaptcha();
        }, 3000);
      });
    } else {
      this.loadnsub.submit = false;
    }
  }

  handleReset(): void {
    this.captchaSuccess = false;
    this.captchaResponse = undefined;
    this.captchaIsExpired = false;
    this.cdr.detectChanges();
  }

  handleSuccess(captchaResponse: string): void {
    this.captchaSuccess = true;
    this.captchaResponse = captchaResponse;
    this.captchaIsExpired = false;
    this.cdr.detectChanges();
  }

  handleLoad(): void {
    this.captchaIsLoaded = true;
    this.captchaIsExpired = false;
    this.cdr.detectChanges();
  }

  handleExpire(): void {
    this.captchaSuccess = false;
    this.captchaIsExpired = true;
    this.cdr.detectChanges();
  }

  public createCanonicalURL() {
    const head = this.dom.getElementsByTagName('head')[0];
    let element: HTMLLinkElement =
      this.dom.querySelector(`link[rel='canonical']`) || null;
    if (element == null) {
      element = this.dom.createElement('link') as HTMLLinkElement;
      head.appendChild(element);
    }
    element.setAttribute('rel', 'canonical');
    element.setAttribute('href', 'https://www.clodura.ai/tools/email-verifier');
  }

  public updateHrefLangs(url: string) {
    // let link: HTMLLinkElement = this.dom.createElement("link");
    // link.setAttribute("rel", "canonical");
    // this.dom.head.appendChild(link);
    // link.setAttribute("href", this.dom.URL.replace("http://", "https://"));
    // console.log(link, this.dom.URL);
    const arr = Array.from(this.dom.head.children);
    const linkElt = arr.find(
      (e: any) => e['rel'] === 'alternate' && e['hreflang'] === 'x-default'
    );
    const linkElten = arr.find(
      (e: any) => e['rel'] === 'alternate' && e['hreflang'] === 'en'
    );
    this.renderer.setAttribute(linkElten, 'href', url);
    this.renderer.setAttribute(linkElt, 'href', url);
  }
}
